import pubfooter from '@/components/pubfooter/pubfooter'
import tools from '@/common/tools.js'
import axios from 'axios'

export default {
  name: 'assessdetails',
  data: function() {
    return {
      commentType: 1,
      nowPage: 1,
      itemId: 0,
      allData: {},
      commonList: [],
      loadingShow: false,
      noDataShow: false,
      busy: true,
      totalPage: 1,
      commentStatistics: {}
    }
  },
  activated() {
    this.itemId = this.$route.query.itemId
    this.commentStatistics = {}
    this._getAssessData()
  },
  deactivated () {
    this.$destroy()
  },
  methods: {
    loadMore() {
      console.log('重复加载', this.busy)
      this.loadingShow = true
      this.busy = true
      // 官方示例中延迟了1秒，防止滚动条滚动时的频繁请求数据
      this.timer = setTimeout(() => {
        // 这里请求接口去拿数据，实际应该是调用一个请求数据的方法
        this.nowPage++
        this._getAssessData()
      }, 1000)
    },
    changeCommentType(num) {
      this.commentType = num
      this.reSetGetData()
      this._getAssessData()
    },
    reSetGetData() {
      this.nowPage = 1
      this.allData = {}
      this.commonList = []
      this.loadingShow = false
      this.noDataShow = false
      this.busy = true
      this.totalPage = 1
    },
    _getAssessData() {
      let url = '/Mobile/Goods/comment.html'
      axios.get(url, {
        params: {
          p: this.nowPage,
          id: this.itemId,
          commentType: this.commentType
        }
      })
        .then((res) => {
          console.log(res)
          if (res.status === 200) {
            this.allData = res.data
            this.commonList = [...this.commonList, ...res.data.comment]
            this.totalPage = res.data.totalpage
            if (Object.keys(this.commentStatistics).length == 0) {
              this.commentStatistics = res.data.commentStatistics
            }
            console.log('当前页：', this.nowPage, this.totalPage)
            if (this.nowPage >= this.totalPage) {
              this.busy = true
              this.loadingShow = false
              this.noDataShow = true
            } else {
              this.busy = false
            }
            /* if (res.data.status === 1) {
              tools.showError(res.data.msg)
            } */
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    dianZan(index, commonId) {
      console.log(index, commonId)
      //
      let url = '/Mobile/Goods/zan_goods.html'
      axios.get(url, {
        params: {
          comment_id: commonId
        }
      })
        .then((res) => {
          console.log(res)
          if (res.status === 200) {
            if (res.data.success === 1) {
              this.commonList[index].iszan = 1
            } else {
              this.commonList[index].iszan = 0
            }
            this.commonList[index].zan_num = res.data.data
            tools.showError(res.data.msg)
            /* if (res.data.status === 1) {
              tools.showError(res.data.msg)
            } */
          }
        })
        .catch((error) => {
          console.log(error)
        })
    }
  },
  components: {
    pubfooter
  }
}



